import { useFormik } from 'formik';
import React, { useRef, useState } from 'react';
import MultipleValueTextInput from 'react-multivalue-text-input';
import app_config from '../../config';

const ExtensionGen = () => {

  const scriptData = `
  console.log('Script running in background');`

  const titleRef = useRef(null);
  const [selectedOptions, setSelectedOptions] = useState([]);
  const [link, setLink] = useState('');
  const [selFile, setSelFile] = useState(null);
  const url = app_config.apiUrl;

  const [selFeatures, setSelFeatures] = useState([]);

  const featureOptions = [
    'Attack', 'Insult', 'Threat', 'Obscene'
  ];

  const generateExtension = async (configOptions) => {
    const res = await fetch('http://localhost:5000/extension/generate', {
      method: 'POST',
      body: JSON.stringify({
        configOptions,
        filename: 'myfile', imagesData: ['icon_48.png', 'icon_128.png'], manifestData: '', scriptData, htmlData: '<h1>My Custom Extension</h1>'
      }),
      headers: {
        'Content-Type': 'application/json'
      }
    });

    const {downloadLink} = await res.json();
    setLink(downloadLink);
    console.log(downloadLink);
  }

  const extForm = useFormik({
    initialValues: {
      title: '',
      icon : '',
      features : []
    },
    onSubmit: async (values) => {
      values.icon = selFile.name;
      values.features = selFeatures;

      console.log(values);
      generateExtension(values);
    }
  });

  const uploadFile = (e) => {
    const file = e.target.files[0];
    const fd = new FormData();
    setSelFile(file);
    fd.append("myfile", file);
    fetch(url + "/util/uploadfile", {
      method: "POST",
      body: fd,
    }).then((res) => {
      if (res.status === 200) {
        console.log("file uploaded");
      }
    });
  };

  return (
    <>
      <title>Form Example</title>
      {/* Bootstrap CSS */}
      <link
        rel="stylesheet"
        href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
      />
      <div className="container mt-5">
        <h1>Form Example</h1>
        <form onSubmit={extForm.handleSubmit}>
          <div className="form-group">
            <label htmlFor="textInput">Text Field</label>
            <input
              type="text"
              className="form-control"
              id="title"
              value={extForm.values.title}
              onChange={extForm.handleChange}
              placeholder="Enter text"
            />
          </div>
          <div className="form-group">
            <label htmlFor="imageInput">Image Field</label>
            <input type="file" className="form-control-file" onChange={uploadFile} />
          </div>
            <h5>Features:</h5>
          <div className="form-group">
            {
              featureOptions.map((feature, index) => (
                <div className="form-check">
                <input
                  className="form-check-input"
                  type="checkbox"
                  checked={selFeatures.includes(feature)}
                  onChange={(e) => {
                    if(e.target.checked){
                      setSelFeatures([...selFeatures, feature]);
                    }else{
                      setSelFeatures(selFeatures.filter((f) => f !== feature));
                    }
                  }}
                />
                <label className="form-check-label">
                  {feature}
                </label>
              </div>
              ))
            }
           
          </div>

          <MultipleValueTextInput
            onItemAdded={(item, allItems) => console.log(`Item added: ${item}`)}
            onItemDeleted={(item, allItems) => console.log(`Item removed: ${item}`)}
            label="Items"
            name="item-input"
            placeholder="Enter whatever items you want; separate them with COMMA or ENTER."
          />
          <button type="submit" className="btn btn-primary">
            Submit
          </button>
          {/* <button type='button' className='btn btn-primary' onClick={generateExtension} >Generate</button> */}
          
          {
            link ?  <a className='btn btn-success'  href={link} >Download</a>
            :
            ''
          }
        </form>
      </div>
      {/* Bootstrap JS */}
    </>


  )
}

export default ExtensionGen